import { Block, Inline } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import Placeholder from '../models';

export default {
  name: 'placeholder',

  match(node: JsonML): boolean {
    return getTagName(node) === 'ph';
  },

  convert(node: JsonML): Block | Inline {
    return Placeholder.create({
      data: getAttributes(node) || {},
    });
  },
};
